.rhythm {
    background-color: #e0e0e0;
    height: 3em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: stretch;
        align-items: stretch;
}

.rhythm + .rhythm {
    border-top: dashed black 1px;
}

.rhythm__title {
    width: 10em;
    -ms-flex-item-align: center;
        align-self: center;
    margin-left: 1em;
}

.rhythm__image {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: stretch;
        align-items: stretch;
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.rhythm__spacer {
    height: 100%;
}

.rhythm__spacer + .rhythm__spacer {
    border-left: solid black 0.1em;
}

.rhythm--user .rhythm__spacer {
    border-color: red;
}


