.levelSelect {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    list-style: none;
    width: 30em;
}

.levelSelect__level {
    cursor: pointer;
    margin: 0.25em;
    padding: 0.75em 2em;
    border: solid grey 1px;
    border-radius: 1em;
}

.levelSelect__level:hover {
    background: #e0e0e0;
}


